<template>
  <view class="header" :style="{ 'padding-top':navBar.top+'px','height':navBar.height+'px','line-height':navBar.height+'px'}">
    <view class="left" :style="{color:color}"  @click="back">
	  <image src="/static/img/home_location.png" mode="" class="home_location right-m" v-if="type==1"></image>
	  <view class="uni-icon uni-icon-back" @click="toBack" v-if="type==0">
	  	
	  </view>
      <view class="title">{{title}}</view>
	  <image src="/static/img/xiajiantou.png" mode="" class="dingwei-img left-m" v-if="type==1"></image>
    </view>
    <view class="flex1">
      <slot></slot>
    </view>
  </view>
</template>

<script>
  import uniIcons from "@/components/uni-icons/uni-icons.vue"

  export default {
    name: 'NavBar',
    components: {uniIcons},
    data() {
      return {
        navBar:{
          top:0,
          height:0
        }
      };
    },
    props: {
      color: {
        type:String,
        default: '#333'
      },
      title: {
        type:String,
        default: ''
      },
	  type:{
		 type:[Number,String],
		  default:0
	  },
      backBtn: {
        type:Boolean,
        default: true
      }
    },
    mounted(){
      //#ifdef APP-PLUS
      this.navBar.top = uni.getSystemInfoSync()['statusBarHeight'];
      this.navBar.height = 40;
      // #endif
      //#ifdef MP
      this.navBar = uni.getMenuButtonBoundingClientRect()
      // #endif

    },
    methods: {
      back:function () {
        uni.navigateBack({
          delta: 1
        });
      }
    }
  }
</script>
<style lang="scss">
	.home_location{
		width:30upx;
		height:36upx;
	}
	.right-m{
		margin-right:20upx;
	}
	.left-m{
		margin-left:10upx;
	}
  .header{
    padding-right: 190upx;
    padding-top: var(--status-bar-height);
    padding-bottom: 16upx;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
	background:linear-gradient(147deg,rgba(255,191,0,1),rgba(251,216,111,1));
    .left{
      display: flex;
      align-items: center;
      padding: 0 40upx;
      .title{
        margin-left: 20upx;
      }
    }
  }
  .flex1{
    flex: 1;
  }
</style>
